.BackImage {
    height: 95vh;
    background-image: linear-gradient(115deg, #3498dba9, #2c3e50a9), url('../../../assets/banner1.png');
    background-size: cover;
    margin-top: 0px;
    position: absolute;
    top: 0;
    z-index: -1;
    width: 100%;
    clip-path: polygon(0 0, 100% 0, 100% 80vh, 0 100%)
}

.TagBox {
    text-align: center;
    margin-top: 35vh;
}

.HeadTag {
    color: white;
    font-family: Helvetica, sans-serif;
    font-size: 75px;
    margin-bottom: 0px;
}

.SubTag {
    font-family: Helvetica, sans-serif;
    font-size: 20px;
    color: #ccc;
    opacity: 0.6;
    margin-top: 0px;
}

.GetStarted {
    background-color: #fd4b4b;
    font-size: 16px;
    width: 160px;
    height: 35px;
    border: 1px solid #fd4b4b;
    border-radius: 4px;
    color: white;
    box-shadow: 1px 2px 1px rgb(63, 59, 59);
    cursor: pointer;
}

@media (max-width: 450px) {
    .HeadTag {
        font-size: 35px;
    }
    .SubTag {
        font-size: 15px;
    }
    .GetStarted {
        width: 130px;
        font-size: 14px
    }
}

@media (max-width: 400px) {
    .HeadTag {
        font-size: 30px;
    }
    .SubTag {
        font-size: 13px;
    }
    .GetStarted {
        width: 120px;
        font-size: 13px;
        height: 30px;
    }
}