.loginbox{
    width: 350px;
    height: 550px;
    background: #fff;
    color: #000;
    top: 50%;
    left: 50%;
    position: absolute;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    box-sizing: border-box;
    padding: 35px 30px;
    margin-top: 15px;
}

h1 {
    font-size: 60px;
}

.loginbox p{
    margin: 0;
    padding: 0;
    font-weight: bold;
}

.loginbox input{
    width: 100%;
    margin-bottom: 20px;
}

.loginbox input[type="email"], input[type="password"] {
    border: none;
    border-bottom: 1px solid;
    background: transparent;
    outline: none;
    height: 40px;
    color: #000;
    font-size: 16px;
}

.loginbox input[type="submit"] {
    background: #303f9f;
    opacity: 0.8;
    color: #fff;
    border: none;
    height: 40px;
    font-size: 18px;
    border-radius: 20px;
}

.loginbox input[type="submit"]:hover {   
    border: 2px solid #303f9f;;
    background: #fff;
    color: #303f9f;
    cursor: pointer;
}

.loginbox a {
    text-decoration: none;
    font-size: 12px;
    line-height: 20px;
    color: darkgrey;
}

.loginbox a:hover
{
    color: #ffc107;
}

.loginbox label {
    font-size: 12px;
}
